.footer_outer_container
{
    background-color: #111;
    color: gray;
    display: flex;
    justify-content: center;
}
.footer_inner_container
{
  width: 60%;
}
.footer_icons
{
    width: 12%;
    display: flex;
    justify-content: space-between;
    color: #fff;
}
.footer_data 
{
    display: flex;
    justify-content: space-between;
     list-style: none;
}

.footer_data ul
{
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 12vh;
}
.footer_data ul li{
    cursor: pointer;
}
.service_code
{
    display: flex;
    align-items: center;
    justify-content: center;
    border: #fff 1px solid;
    width: 10%;
    margin: 10% 0;
    cursor: pointer;
}
.copy-write
{
    font-size: 11px;
    margin-bottom: 10px;
}